.sort {
  width: 100%; }
  .sort .sort__field {
    width: 85%;
    display: inline-block; }
  .sort .sort__order {
    border: 1px solid #ccc;
    border-radius: 2px;
    color: #333;
    height: 30px;
    padding: 5px;
    cursor: pointer;
    vertical-align: bottom;
    width: 15%;
    margin: 10px 0 10px -1px;
    background-color: #fff; }
    .sort .sort__order:hover {
      background-color: #f1f1f1; }
  .sort .sort__order-img {
    width: 100%;
    height: auto;
    max-height: 20px;
    max-width: 25px; }

@media (min-width: 1024px) {
  .sort {
    width: 200px;
    display: inline-block;
    margin-right: 15px; }
    .sort .sort__field {
      margin: 5px 0;
      border-radius: 4px 0 0 4px;
      -webkit-appearance: none; }
    .sort .sort__order {
      margin: 5px 0 5px -1px;
      border-radius: 0 4px 4px 0; } }
