.pagination {
  display: inline-block; }
  .pagination .pagination__number {
    color: #333;
    font-weight: 300;
    float: left;
    font-size: 13px;
    padding: 8px 16px;
    text-decoration: none;
    transition: background-color .3s;
    border: 1px solid #ddd;
    margin: 0 4px;
    cursor: pointer; }
    .pagination .pagination__number.pagination__number--active {
      background-color: #c31017;
      color: white;
      border: 1px solid #c31017; }
      .pagination .pagination__number.pagination__number--active:focus {
        outline: none; }
    .pagination .pagination__number:hover:not(.pagination__number--active) {
      background-color: #ddd; }
