.restaurant-items {
  padding: 15px 25px;
  background-color: #e2e2e2; }
  .restaurant-items .restaurant-items__section-name {
    margin: 5px 0;
    font-size: 16px;
    font-weight: 600;
    color: #333; }
  .restaurant-items .restaurant-items__section-items {
    padding: 0; }
  .restaurant-items .restaurant-items__section-item {
    list-style-type: none;
    margin: 25px 0; }
  .restaurant-items .restaurant-items__item-name {
    font-size: 14px;
    font-weight: 300;
    color: #333;
    width: 45%;
    display: inline-block; }
  .restaurant-items .restaurant-items__item-price {
    margin-right: 15px;
    font-size: 14px;
    color: #333;
    width: 13%;
    display: inline-block; }
  .restaurant-items .restaurant-items__item-buy {
    text-decoration: none;
    color: #fff;
    font-size: 12px;
    padding: 5px 10px;
    background-color: #c31017;
    border-radius: 2px;
    width: auto;
    cursor: pointer; }

@media (min-width: 1024px) {
  .restaurant-items .restaurant-items__item-name {
    min-width: 150px;
    width: auto; }
  .restaurant-items .restaurant-items__item-price {
    min-width: 100px;
    width: auto; }
  .restaurant-items .restaurant-items__section-item {
    margin: 15px 0; } }
