.restaurant-detail .restaurant-detail__back {
  border: none;
  border-radius: 2px;
  padding: 10px 12px;
  color: #333;
  background-color: #e2e2e2; }

.restaurant-detail .restaurant-detail__info {
  padding: 10px 0 15px; }

@media (min-width: 1024px) {
  .restaurant-detail .restaurant-detail__back {
    padding: 5px 8px;
    cursor: pointer; }
  .restaurant-detail .restaurant-detail__info {
    padding: 10px 0 15px; } }
