.restaurant-info {
  text-align: center;
  border-radius: 2px;
  padding: 15px 10px;
  margin: 15px 0; }
  .restaurant-info.restaurant-info--clickable {
    background-color: #e2e2e2;
    cursor: pointer;
    transition: background-color 0.2s ease; }
    .restaurant-info.restaurant-info--clickable:hover {
      box-shadow: 0 0 3px #aaa;
      background-color: #f1f1f1; }
  .restaurant-info .restaurant-info__logo {
    max-width: 100px; }
  .restaurant-info .restaurant-info__title {
    font-size: 18px;
    color: #333;
    margin: 5px; }
  .restaurant-info .restaurant-info__rating {
    font-size: 14px;
    color: #666;
    margin-right: 10px; }
    .restaurant-info .restaurant-info__rating small {
      color: #999;
      font-size: 12px; }
  .restaurant-info .restaurant-info__location {
    font-size: 12px;
    color: #666;
    margin: 0 0 10px;
    display: inline-block; }
  .restaurant-info .restaurant-info__category {
    border: 1px solid #ccc;
    display: inline-block;
    padding: 3px 10px;
    font-size: 12px;
    margin-right: 5px;
    color: #333; }

@media (min-width: 1024px) {
  .restaurant-info {
    text-align: left;
    padding: 15px 20px; }
    .restaurant-info .restaurant-info__logo {
      display: inline-block;
      width: 10%; }
    .restaurant-info .restaurant-info__content {
      display: inline-block;
      width: 80%;
      padding: 0 20px;
      vertical-align: top; }
    .restaurant-info .restaurant-info__title {
      margin: 0 20px 0 0;
      display: inline-block; }
    .restaurant-info .restaurant-info__location {
      display: block;
      margin: 5px 0 15px; }
    .restaurant-info .restaurant-info__rating {
      margin-right: 0; }
    .restaurant-info .restaurant-info__category {
      padding: 5px 20px;
      margin-right: 15px; } }
