body {
  margin: 0;
  background-color: #c31017;
  font-family: 'Lato', sans-serif;
  background-image: url(/assets/bg.png); }

* {
  box-sizing: border-box; }

.container {
  margin: 50px auto;
  background-color: #eee;
  padding: 20px 10px;
  border-radius: 2px; }

@media (min-width: 1024px) {
  .container {
    max-width: 1000px;
    padding: 30px;
    box-shadow: 3px 3px 20px rgba(0, 0, 0, 0.6);
    border-radius: 4px; } }
