.restaurants {
  text-align: center; }
  .restaurants .restaurants__logo-img {
    height: 120px;
    width: 220px; }
  .restaurants .restaurants__list {
    margin-top: 30px; }
  .restaurants .restaurants__list-actions {
    text-align: right;
    margin-right: 5px; }
  .restaurants .restaurants__list-restaurants {
    margin: 0;
    padding: 5px;
    position: relative;
    min-height: 200px; }
    .restaurants .restaurants__list-restaurants.restaurants__list-restaurants--loading:before {
      content: '';
      background-image: url(/assets/loading.gif);
      position: absolute;
      left: 0;
      right: 0;
      top: 30%;
      bottom: 0;
      margin-left: auto;
      margin-right: auto;
      width: 100px;
      height: 100px;
      z-index: 2; }
    .restaurants .restaurants__list-restaurants.restaurants__list-restaurants--loading:after {
      content: '';
      position: absolute;
      width: 100%;
      height: 95%;
      z-index: 1;
      top: 20px;
      left: 0;
      padding: 30px;
      border: 2px solid #e6e6e6;
      box-sizing: border-box;
      background-color: rgba(255, 255, 255, 0.9); }
  .restaurants .restaurants__list-filter {
    display: inline-block; }

@media (min-width: 1024px) {
  .restaurants {
    text-align: left; } }
